export default {
	data() {
		return {
			// 
			share: {
				title: '', //
				path: '/pages/home/index', //  
				imageUrl: '/static/img/shardimg.jpg', // ()
			}
		}
	},
	// 
	// 1.
	onShareAppMessage(res) {
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
		}
	},
	//2.
	onShareTimeline(res) {
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
		}
	},
}